import * as React from 'react';
import { NoteElement } from '../App';

interface TextInputProps {
  notes: NoteElement[];
  activeId: number;
  onTextChange(text: string): void;
}

interface TextInputState {
  value: string;
  disabled: boolean;
}

class TextInput extends React.Component<TextInputProps, TextInputState> {

  constructor(props: TextInputProps) {
    super(props);
    this.state = {
      value: '',
      disabled: true
    };
  }

  componentWillReceiveProps(nextProps: TextInputProps) {
    const activeNote: HTMLLIElement | null = document.querySelector('.note--active') as HTMLLIElement;
    if (nextProps.activeId !== -1 && activeNote !== null && nextProps.notes[nextProps.activeId] !== undefined) {
      this.setState({
        value: nextProps.notes[nextProps.activeId].text,
      });
    } else {
      this.setState({value: ''});
      if (nextProps.notes.length === 0) {
        this.setState({disabled: true});
      } else {
        this.setState({disabled: false});
      }
    }
  }

  onChange = (e: React.SyntheticEvent<HTMLTextAreaElement>) => {
    const { notes, activeId } = this.props;
    // If note exist in notes array
    if (notes[activeId]) {
      console.log('changing textarea value');
      this.setState({ value: e.currentTarget.value }, () => {
        this.props.onTextChange(this.state.value);
      });
    } else {
      // if note not exist (notes array is empty)
      console.log('note is not exist');
      this.setState({ value: '' }, () => {
        this.props.onTextChange(this.state.value);
      });
    }
  }

  render() {
    return (
      <textarea
        value={this.state.value}
        onChange={e => this.onChange(e)}
        disabled={this.state.disabled}
      />
    );
  }
}

export default TextInput;



